import Independent from "../Independent";
import { ProductBuy } from "../Context/RestApiContext";


function Notindependent() {
    const product = ProductBuy();
        console.log(product)
    return ( 
        <>
             <Independent />
            <br></br>
            {
                product.map((item) => {
                    return ( 
                        <>
                            <img src={item.image} />
                        </>
                    )
                })
            }
        </>
    )

}

export default Notindependent;