import Independent from "../Independent";

import { SayMe } from "../Context/RestApiContext";







function Unmember() {
    const sayname = SayMe()
    return ( 
        <>
             <Independent />
            <br></br> <br></br>
            <h1>Unmember STATE  {sayname[2]}</h1>
        </>
    )

}

export default Unmember;