import axios from "axios";
import { useEffect, useState } from "react";
import { AiFillCar } from "react-icons/ai";



function Google() {
    const baseURL = 'https://restcountries.com/v3.1/name';
    const [search, setSearch] = useState('');
    const [data, setData] = useState([])
    const handleChange = (e) => {
        setSearch(e.target.value)
    }

    const fetchSearch = (country) => {
        axios(`${baseURL}/${country}`).then((response) => {
            if (response.status === 200) {
                console.log(response.data)
                setData(response.data)
            } else if (response.status === 404) {
                console.log('worng error', response.data.message)
                alert('error')
            }

        }).catch((err) => {
            console.log(err)
        })
    }
    useEffect(() => {

    }, [])

    const clickBtn = () => {
        fetchSearch(search)
    }
    return (
        <>
            <div className="  " style={{ width: '60%', margin: 'auto' }}>
                <h2 className="fw-bolder">GOOGLE</h2> <br></br>
                <input value={search} onChange={handleChange} type="text" placeholder="enter word " className="form-control" />
                <button onClick={clickBtn} className="mt-3 fw-bolder btn btn-dark">search</button>

               


            </div>
            <div className="row">
                    { data.length > 0 ?    data.map((item, id) => {
                        return (
                            <div className="col-4  text-black   container-fluid mb-5" key={id} style={{ width: '60%', margin: 'auto' }}  >
                                <hr></hr>
                                <h3 className="h2 fw-bold text-center">{item.status}</h3>
                                <div className="card mt-4 " style={{ padding: '10px' }}>
                                    <h4>{item.name.official}</h4>
                                    <div className="mt-4">
                                        <img className="card-img-top image-fluid" alt="..." src={item.flags.png} style={{ width: '100%', boxSizing: 'border-box', height: '160px' }} />
                                        <br />

                                    </div>
                                    <div className="fw-bold mt-4">
                                        <p className="fw-bold">Population-- <span>{item.population}</span></p>
                                        <p className="fw-bold">Region-- <span>{item.region}</span></p>
                                        <p className="fw-bold">Time Zones-- <span>{item.timezones[0]}</span></p>
                                        <p className="">Subregion-- <span>{item.subregion}</span></p>
                                        <br></br>
                                        <hr></hr>
                                        <div className="d-flex justify-content-space-between">
                                            <h2 className="mt-2"><AiFillCar /></h2>
                                            <h2 className="mt-2 ps-3">{item.car.side}</h2>

                                        </div>

                                    </div>
                                </div>

                            </div>)
                    })
                    :<h1 className="fw-bolder text-center">EMPTY INPUT</h1>}
                </div>

        </>
    )
}

export default Google;